/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0;

}



/**
 * Basic styling
 */
body {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
     -moz-font-feature-settings: "kern" 1;
       -o-font-feature-settings: "kern" 1;
          font-feature-settings: "kern" 1;
  font-kerning: normal;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  overflow-wrap: break-word;
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
  margin-bottom: $spacing-unit / 1.6;
}

hr {
  margin-top: $spacing-unit;
  margin-bottom: $spacing-unit;
}

/**
 * `main` element
 */
main {
  display: block; /* Default value of `display` of `main` element is 'inline' in IE 11. */
}



/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle;
}



/**
 * Figures
 */
figure > img {
  display: block;
}

figcaption {
  font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
  margin-left: $spacing-unit;
}

li {
  > ul,
  > ol {
    margin-bottom: 0;
  }
}



/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  display: inline-block;
  margin-left: -5px; // -25px;
  padding: 2px 5px 0px 5px; // 25px;
  //background: $header-background-color;
  // border-bottom: 3px solid $brand-color;
  font-weight: $base-font-weight;
  a {
    color: #111;
  }
  a:visited {
    color: #555;
  }
}



/**
 * Links
 */
a {
  color: $link-base-color;
  text-decoration: none;

  &:visited {
    color: $color-link-visited;
  }

  &:hover {
    color: $text-color;
    text-decoration: underline;
  }

  .social-media-list &:hover {
    text-decoration: none;

    .username {
      text-decoration: underline;
    }
  }
}


/**
 * Blockquotes
 */
blockquote {
  color: $text-light-color;
  background-color: $blockquote-background-color;
  border-left: 4px solid $brand-color-light;
  padding: 15px $spacing-unit / 2;
  @include relative-font-size(1.05);
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }
}



/**
 * Code formatting
 */
pre,
code {
  font-family: $code-font-family;
  font-size: 0.8375em;
  border: 1px solid $brand-color-light;
  border-radius: 3px;
  background-color: #eee;
}

code {
  padding: 1px 5px;
}

pre {
  padding: 8px 12px;
  overflow-x: auto;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}

.highlight {
  border-radius: 3px;
  background: $code-background-color;
  @extend %vertical-rhythm;

  .highlighter-rouge & {
    background: $code-background-color;
  }
}



/**
 * Wrapper
 */
.wrapper {
  max-width: calc(#{$content-width} - (#{$spacing-unit}));
  margin-right: auto;
  margin-left: auto;
  padding-right: $spacing-unit / 2;
  padding-left: $spacing-unit / 2;
  @extend %clearfix;

  @media screen and (min-width: $on-large) {
    max-width: calc(#{$content-width} - (#{$spacing-unit} * 2));
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
  }
}



/**
 * Clearfix
 */
%clearfix:after {
  content: "";
  display: table;
  clear: both;
}



/**
 * Icons
 */

.orange {
  color: #f66a0a;
}

.grey {
  color: #828282;
}

.svg-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  fill: currentColor;
  padding: 5px 3px 2px 0;
  vertical-align: text-bottom;
}


/**
 * Tables
 */
table {
  margin-bottom: $spacing-unit;
  width: 100%;
  text-align: $table-text-align;
  color: $table-text-color;
  border-collapse: collapse;
  border: 1px solid $table-border-color;
  tr {
    &:nth-child(even) {
      background-color: $table-zebra-color;
    }
  }
  th, td {
    padding: ($spacing-unit / 3) ($spacing-unit / 2);
  }
  th {
    background-color: $table-header-bg-color;
    border: 1px solid $table-header-border;
  }
  td {
    border: 1px solid $table-border-color;
  }

  @include media-query($on-laptop) {
    display: block;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
            -ms-overflow-style: -ms-autohiding-scrollbar;
  }
}

.tags {
  //margin-bottom: 20px;
}
.tag {
  display: inline-block;
  margin-right: 7px;
  margin-bottom: 5px;
  // background: $code-background-color;
  border-radius: 3px;
  a {
    color: $color-tag;
  }
}
h1, h2, h3, h4, h5, h6 {
  .tag {
    display: block;
    background: none;
    padding: 0;
    margin: 0 0 0 10px;
    border-radius: none;
    font-size: $base-font-size * 0.925;
    span {
      a, a:visited {
        color: $color-tag;
      }
      display: inline-block;
      padding: 0px 6px;
      margin-right: 5px;
      margin-bottom: 5px;
      border-radius: 3px;
    }
  }
}

.aside {
  color: $text-lighter-color;  
}

.todo.TODO {
  color: $color-todo;
  font-weight: 600;
}

.posts-list, .notes-list, .drafts-list {
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0;
  }
  .draft, .note, .post {
    margin-bottom: $spacing-unit * 1.2;
  }
  h2.post-title {
    margin-bottom: $spacing-unit * 0.2;
  }
	.post-meta {
		margin-bottom: $spacing-unit / 2;
	}
  .draft, .note {
    .date {
      font-weight: 600;
    }
    .dt-modified {
      color: #aaa;
    }
  }
}

object, .chart {
  width: 100%;
}

#table-of-contents {
	h2 {
		margin-top: 0;
		font-size: 1.6rem;
	}
	margin-bottom: $spacing-unit * 1.0;
}

.paginated-post {
	h1 {
		margin-bottom: 0;
	}
	.post-meta {
		margin-bottom: $spacing-unit / 2;
	}
  .date {
    font-weight: 600;
  }
  .dt-modified {
    color: #aaa;
  }
}

ul.footer-links {
	padding: 0;
	margin: 0;
	li {
		list-style-type: none;
		a, a:visited {
			color: $brand-color;
		}
	}
}

ul.org-ul {
  padding-left: 30px;
  margin-left: 0px;
  li.on, li.off, li.trans {
    list-style-type: none;
  }
  li {
    ul.org-ul {
      // margin-left: 25px;
    }
  }
}

input[type="checkbox"].org-checkbox {
  font-size: 1.6em; 
  margin: 0 8px 10px -28px;
}

input[type="checkbox"]:disabled {
  filter: invert(-25%);
}

div.SOURCE {
  background: #ddd;
  border-radius: 2px;
  padding: 5px 10px;
  font-family: monospace;
  white-space: nowrap;
  overflow: hidden;
  overflow-x: auto;
}
div.text-note-example {
  background: #eee;
  border-radius: 2px;
  padding: 5px 10px 10px;
  margin-bottom: $spacing-unit;
}

.org-src-container {
  .org-comment-delimiter, .org-comment {
    padding: 2px 2px 2px 0;
    background: #ddd;
    color: #666;
  }
  .org-keyword {
    color: #7f69fa;
  }
}
.timestamp {
  padding: 3px 5px;
  background: #eee;
  border-radius: 2px;
  .time {
    font-weight: bold;
  }
}
mark.highlight {
  border-radius: 2px; 
  background-color: #ffffa2;
  padding: 2px 3px;
}
.underline {
  text-decoration: underline;
}
.dt-published {
  color: #333;
  font-weight: 600;
}
.dt-modified {

}
a.internal-link {

}

main a.internal-link {
  &:hover {
    &::after {
      background-color: #999 !important;
    }
  }

  &::after {
    content: "";
    opacity: 1.0; // 0.85;

    width: 1.0em;
    height: 1.0em;
    display: inline-block;
    vertical-align: text-bottom;

    margin-left: 0.25em;

    -webkit-mask-repeat: no-repeat;
            mask-repeat: no-repeat;

    -webkit-mask-image: url('/images/icons/link.svg');
            mask-image: url('/images/icons/link.svg');

    -webkit-mask-size: cover;
            mask-size: cover;

    background-color: #ddd;
  }
}

.comments-headline {
  margin-top: 20px;

  @include relative-font-size(2.2);
  letter-spacing: -1px;
  line-height: 1.15;
  font-weight: 600;

  @media screen and (min-width: $on-large) {
    @include relative-font-size(2.2);
  }

}
